
main {
	margin: 0 auto;
	max-width: 90%;
	padding: 50px 30px;
}

:root {
	--primary: #96ACB7;
	--secondary: #E6F9AF;
	--tertiary: #E9AFA3;
}

body {
  font-family: Work Sans;
  text-align: center;
  background-color: var(--secondary);

}

.navbar, .navbar-brand {
  text-align: center;
  display: block;
  width: 100%;
  background-color: var(--secondary);
  
}

div.navbar-brand {
  padding-top: 2em;
  margin-bottom: 2em;
  
}

.navbar .navbar-brand > .navbar-item,
.navbar .navbar-brand .navbar-link {
  display: inline-block;
}

main {
  margin-top: 2em;
  margin-bottom: 2em;
  margin-left: 30em;
  margin-right: 30em;
}

.my-pic {
	clip-path: circle();
  width: 250px;
}

.label {
  text-align: left;
}

footer.footer {
  background-color: var(--primary);
}



@media only screen and (max-width: 1024px) {
    main {
      margin-top: 2em;
      margin-bottom: 2em;
      margin-left: 2em;
      margin-right: 2em;
    }
}